<template>
  <div class="my">
    <div class="box">
      <div class="outgo-box" @click="goPrenext">
        <van-icon name="arrow-left" size="22" /><span class="outgo">返回</span>
      </div>
      <div class="mi-cont" @click="miData">
        <span>编辑资料</span>
      </div>
    </div>
    <!-- <h1>我的主页</h1> -->
    <div class="personalData">
      <div class="headPortrait">
        <img :src="imgStr" alt="" @click="portraitText" />
      </div>

      <div class="data-img" @click="showPopup">
        <span>个人资料</span>
      </div>
      <van-popup
        v-model="show"
        position="bottom"
        closeable
        :style="{ height: '80%' }"
      >
        <div class="pop-box">
          <h3>基本资料</h3>
          <div class="pop-box-name one">
            <div class="left">用户名</div>
            <div class="right">{{ personal.prename }}</div>
          </div>
          <div class="pop-box-sex">
            <div class="left">性别</div>
            <div class="right">{{ personal.presex }}</div>
          </div>
          <div class="pop-box-hobby">
            <div class="left">兴趣</div>
            <div class="right">{{ personal.preinterest }}</div>
          </div>
        </div>
      </van-popup>
      <div @click="likeStr">
        <Mycont :msgData="msgData" />
      </div>
      <div @click="replyStr">
        <Mycont :msgData="msgData2" />
      </div>
    </div>
    <div class="set-up" @click="setUp">
      <van-icon name="setting-o" size="42" />
      <div>设置</div>
    </div>
    <van-popup
      v-model="outshow"
      position="bottom"
      closeable
      :style="{ height: '30%' }"
    >
      <div class="get-out" @click="cancellation">注销登录</div>
      <div class="destroy" @click="outLogin">退出登录</div>
    </van-popup>
  </div>
</template>

<script>
import Mycont from "../components/Mycont.vue";
export default {
  components: { Mycont },
  name: "my",
  data() {
    return {
      imgStr: require("../assets/rto.png"),
      errorStr: "",
      show: false,
      outshow: false,
      msgData: {
        cont: "我的收藏",
        icon: "arrow",
      },
      msgData2: {
        cont: "消息中心",
        icon: "arrow",
      },
      personal: {},
      token:'',
    };
  },
  created() {
    let tok = localStorage.getItem('token') || ''
    // console.log(tok);
    if(tok !== ''){
        this.token = tok
        // console.log(this.token);
    }else{
        this.$toast("还没登录")
        setTimeout(()=>{
            this.$router.push({name:'login'})
        },1000)
        // return;
    }
    
    let str = JSON.parse(localStorage.getItem("str")) || [];
    let preser = JSON.parse(localStorage.getItem("preser")) || {};
    this.personal = preser;
    // console.log(this.personal);
    if (str.length >= 1) {
      // console.log(str[str.length-1]);
      this.imgStr = str[str.length - 1].url;
    }
  },
  methods: {
    // 返回上一级
    goPrenext() {
      this.$router.go(-1);
    },

    // 点击头像触发编辑资料
    portraitText() {
      // console.log(this.imgStr);
      // this.$imagePreview([this.imgStr]);
    },
    // 编辑资料事件
    miData() {
      this.$router.push({ name: "midata" });
    },
    // 显示个人资料
    showPopup() {
      let preser = JSON.parse(localStorage.getItem("preser")) || {};
      if (preser !== {}) {
        this.show = true;
      } else {
        this.show = false;
        this.$toast("请先编写个人资料");
        this.$router.push({ name: "midata" });
      }
    },
    // 点进我的收藏
    likeStr() {
      this.$router.push({ name: "likelist" });
    },
    // 点进我的消息
    replyStr() {
      this.$router.push({ name: "replypage" });
    },
    setUp() {
      this.outshow = true;
    },
    // 退出登录
    outLogin() {
      //    console.log('123');

      this.$dialog
        .confirm({
          title: "提示",
          message: "是否退出登录",
          showCancelButton: true,
        })
        .then(() => {
          // on confirm
          localStorage.clear();
          this.$router.push({ name: "login" });
        })
        .catch(() => {
          // on cancel
        });
    },
    // 注销账号
    cancellation() {
      this.$dialog
        .confirm({
          title: "注销",
          message: "确定注销账号？",
          showCancelButton: true,
        })
        .then(() => {

          this.axios({
            method: "post",
            url: "http://www.kangliuyong.com:10002/destroyAccount",
            params:{
                appkey:'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=',
                tokenString:this.token,
            }
          })
            .then((result) => {
            //   console.log(result);
              this.$toast(result.data.msg);

              // on confirm
              localStorage.clear();
              this.$router.push({ name: "login" });
              if (result.data.code === 700) {
                setTimeout(() => {
                  this.$router.push({ name: "login" });
                }, 1000);
              }
              if (result.data.code === "G001") {
                setTimeout(() => {
                  this.$router.push({ name: "login" });
                }, 1000);
              }
            })
            .catch((err) => {});
        })
        .catch(() => {
          // on cancel
        });
    },
  },
  mounted() {},
};
</script>

<style lang="less" scoped>
.data-img {
  width: 60px;
  height: 16px;
  margin: 0 auto 30px;
  text-align: center;
}
.destroy {
  width: 100%;
  height: 50px;
  text-align: center;
  line-height: 50px;
  color: #e4393c;
  font-size: 16px;
  font-weight: bold;
  margin-top: 10px;
  background-color: rgb(240, 239, 239);
}
.get-out {
  width: 100%;
  height: 50px;
  text-align: center;
  line-height: 50px;
  color: #e4393c;
  font-size: 16px;
  margin-top: 45px;
  font-weight: bold;
  background-color: rgb(240, 239, 239);
}
.set-up {
  width: 100%;
  text-align: center;
  position: fixed;
  bottom: 30px;
  div{
      font-size: 16px;
  }
}
.box {
  padding: 10px 0 0 0px;
  display: flex;
  justify-content: space-between;
  .outgo-box {
    position: relative;
    .outgo {
      display: inline-block;
      width: 50px;
      font-size: 18px;
      position: absolute;
      top: -2px;
    }
  }
  .mi-cont {
    margin-right: 10px;
    span {
      display: inline-block;
      font-size: 16px;
    }
  }
}
.personalData {
  //   width: 100%;
  margin: 0 auto;
}
.headPortrait {
  width: 80px;
  height: 80px;
  margin: 0 auto 10px;
  img {
    display: inline-block;
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
}

.pop-box {
  width: 90%;
  padding: 10px 10px 10px 10px;
  background-color: rgb(241, 233, 233);
  margin: 20px auto;
  border-radius: 5px;
  box-shadow: 2px 2px 4px 2px rgba(58, 51, 51, 0.2);
  h3 {
    margin: 0;
    font-weight: 400;
  }
  > div {
    display: flex;
    padding: 10px;
    font-size: 14px;
    border-top: 1px solid #ddd;
    .left {
      width: 80px;
      color: rgb(160, 153, 153);
    }
    .right {
      // padding: ;
      color: rgb(48, 42, 42);
    }
  }
  .one {
    border: none;
    margin-top: 10px;
  }
}
</style>